.coinContainer {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  line-height: 22px;
  letter-spacing: 0.03em;
  height: 100%;
  color: #ffffff;

  cursor: pointer;

  box-shadow: none;
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
  background: transparent !important;
  padding: 0;
  min-width: fit-content;
  z-index: 1;
}

.wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: fit-content;
  align-items: center;
  height: 100%;
  padding: 0 10px;

  @media (max-width: 500px) {
    padding-left: 15px;
    padding-top: 2px;
  }
}

.coinImgWrapper {
  display: flex;
  align-items: center;

  @media (max-width: 500px) {
    font-size: 10px;
  }
}
.coinImg {
  width: 21px;
  height: 21px;
  margin-right: 5px;
  cursor: pointer;

  @media (max-width: 500px) {
    width: 15px;
    height: 15px;
  }
}
.balanceWrapper {
  @media (max-width: 500px) {
    font-size: 11px;
  }
}
.caret {
  padding-left: 7px;
  transition: all 0.3s;
}
.revert {
  transform: rotateX(180deg);
}
