@import "sass/index";

$transition: cubic-bezier(0.4, 0, 0.6, 1) 195ms;

.appBar {
  position: fixed;
  min-height: 50px;
  z-index: 1201;
  box-shadow: none;
  overflow: hidden;
  background-color: use(--template-appBar-backgroundColor);
  transition: width $transition, margin $transition;

  width: 100%;
  display: flex;
  box-sizing: border-box;
  flex-shrink: 0;
  flex-direction: column;

  color: #fff;

  @media (min-width: 600px) {
    min-height: 64px;
  }
}

.toolbar {
  display: flex;
  position: relative;
  align-items: center;

  padding-left: 16px;
  padding-right: 16px;

  @media (min-width: 600px) {
    padding-left: 24px;
    padding-right: 24px;
    min-height: 64px;
  }

  min-height: 56px;

  @media (min-width: 0px) and (orientation: landscape) {
    min-height: 48px;
  }
}

.grow {
  flex: 1 1 auto;
}
.menuButton {
  margin-left: -12px;

  & svg {
    min-width: 32px;
    min-height: 32px;
    & path {
      fill: use(--template-appBar-menu-fill);
    }
  }
}
.mobile {
  display: block;
  @media (min-width: $mobileScreen) {
    display: none;
  }
}

.banner {
  flip: false;
  display: none;

  @media (min-width: $middleTabletScreen) {
    display: block;
  }

  & > * {
    flip: false;
  }
}

.pasinoLogo {
  height: 46px;
  width: 135px;
  margin: 0 spacing(3.5);

  & path {
    fill: use(--template-appBar-menu-fill);
  }

  display: block;
  @media (max-width: 445px) {
    display: none;
  }
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  background: use(--template-page-background);
}

.walletIcon {
  @media (max-width: 340px) {
    margin-left: -12px;
  }

  & svg {
    opacity: $iconsOpacity;
  }
  & svg g {
    stroke: use(--template-appBar-icons-wallet-background);
  }

  &:hover {
    & svg {
      opacity: 1;
    }
  }
}

.helpIcon {
  & svg path {
    fill: use(--template-appBar-icons-help-background);
    opacity: $iconsOpacity;
  }
  & svg circle #Oval {
    fill: use(--template-appBar-icons-help-background);
    opacity: $iconsOpacity;
  }
  &:hover {
    & svg {
      opacity: 1;
    }
  }
}

$avatarSize: 36px;

.userIcon {
  position: absolute;
  top: 0;
  left: 0;
  img {
    width: $avatarSize;
    height: $avatarSize;
  }
}
.menuIcon {
}

.avatarButton {
  position: relative;
  width: 71px;
  height: $avatarSize;
  min-width: 71px;
  padding: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 100px;
  box-shadow: none;

  display: flex;
  justify-content: flex-end;
  @media (max-width: 500px) {
    display: none;
  }
}
.loginBtn {
  border: 0.965px solid #4835ea;
  @media (max-width: 600px) {
    font-weight: 700;
    font-size: 10.2933px;
    line-height: 12px;
  }
}

.feyorra {
  @media (max-width: $feyorraDisableSize) {
    display: none;
  }
}
