@import "sass/index";
@import "./variables.scss";

.paper {
  width: 900px;
  height: 540px;
  position: relative;
  @media (max-width: $mobileScreen) {
    width: 401.25px;
    height: 240px;
    overflow: hidden;
  }
}

.contentWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 30px;
}

.wheelsWrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-self: center;
  z-index: 2;
  left: $absoluteOffset;
  top: 5px;

  @media (max-width: $mobileScreen) {
    display: block;
    left: 52px;
    top: 6px;
  }
}

.boardWrapper {
  position: relative;
  right: $absoluteOffset;
  @media (max-width: $mobileScreen) {
    right: 58px;
    top: -9px;
  }
}
