.root {
  position: absolute;
  height: 100%;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );
  left: 0;
  transition: transform 0.3s;
  pointer-events: none;
  align-self: center;
  border: 2px solid rgba(139, 125, 255, 0.7);
  box-shadow: 0px 0px 24px rgba(72, 53, 234, 0.2);
  border-radius: 6px;
}
