@import "sass/index";

.root {
  position: relative;
  padding: 0;
  margin-right: 2px;

  @media (max-width: 600px) {
    display: none;
  }
}

.button {
  padding: 0;
  height: 100%;
  width: 100%;
}

.ring {
  cursor: pointer;

  & path {
    fill: use(--primary-contrastIcon);
  }

  &:hover path {
    opacity: 1;
  }
}
.desktop {
  display: none;
  @media (min-width: $mobileScreen) {
    display: block;
  }
}

.notification {
  position: absolute;
  top: 5px;
  right: 5px;
  z-index: 2;

  border-radius: 50%;
  background: #de3939;
  color: white;
  width: 16px;
  height: 16px;

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: "Varela Round", sans-serif;
  font-size: 10px;

  line-height: 16px;
}

@keyframes missedNote {
  0% {
    stroke: use(--template-appBar-icons-user-background);
  }
  50% {
    stroke: rgba(0, 231, 255, 0.6);
  }
  100% {
    stroke: use(--template-appBar-icons-user-background);
  }
}

.missed {
  & path {
    animation: missedNote 1s ease-in-out infinite;
  }
}
