@import "sass/index";

$bottomMargin: 100px;

.root {
  bottom: 0;
  position: absolute;
  z-index: 0;

  width: 100vw;

  @media (max-width: $mobileScreen) {
    padding: 0 22px;
  }
}

.bottomSnow {
  width: 100%;
  min-width: 600px;
  margin-bottom: $bottomMargin;
}

.bottomSnowBlock {
  background: #ebf1f4;
  height: $bottomMargin;
  width: 100%;
}

.bottomSnowBlock,
.bottomSnow {
  position: absolute;
  bottom: 0;
  left: 0;
}

.treeWrapper {
  position: absolute;
  bottom: 0;
  width: 100vw;
  display: flex;

  justify-content: center;
  align-items: flex-end;
}

.rightLine {
  position: relative;
  width: 50vw;

  @media (max-width: 840px) {
    display: flex;
    justify-content: center;
  }

  @media (max-width: $mobileScreen) {
    margin-bottom: 50px;
  }
}

.leftLine {
  width: 50vw;

  @media (max-width: 840px) {
    display: none;
  }
}

.snowLineLeft {
  position: absolute;
  bottom: 0;
  left: -550px;
}
.snowLineRight {
  position: absolute;
  bottom: 0;
  right: -550px;
}

.tree {
  position: absolute;
  left: 0;
  bottom: 150px;

  @media (max-width: 1080px) {
    bottom: 100px;
  }
}
.treeGifts {
  position: absolute;
  bottom: 60px;
  right: 0;

  @media (max-width: 840px) {
    right: auto;
  }
}
