@import "sass/index";
@import "../../variables.scss";

.descWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: $descriptionContentWidth;
  height: $descriptionContentHeight;
  background-image: url("../../assets/descriptor.webp");
  background-size: $descriptionContentWidth;
  gap: 8px;
  @media (max-width: $mobileScreen) {
    width: $descriptionMobileContentWidth;
    height: 26px;
    background-size: $descriptionMobileContentWidth;
    gap: 5px;
  }
}

.white {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  font-size: 15.1139px;
  line-height: $descriptionLineHeight;
  letter-spacing: 0.02em;
  color: white;
  @media (max-width: $mobileScreen) {
    font-size: 8px;
    line-height: $descriptionLineHeightMobile;
  }
}

.yellow {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 1000;
  font-size: 16px;
  line-height: $descriptionLineHeight;
  letter-spacing: 0.02em;
  color: #fff500;
  @media (max-width: $mobileScreen) {
    font-size: 7px;
    line-height: $descriptionLineHeightMobile;
  }
}
