.root {
  position: fixed;
  z-index: 5;
  bottom: 0;
  right: 0;
  height: calc(100% - 56px);
  transition: transform 0.4s;

  @media (min-width: 601px) {
    display: none;
  }
}
.overlay {
  background: linear-gradient(93.31deg, #24375f -74.18%, #000000 69.38%),
    #0a0d18;
  position: fixed;
  bottom: 90px;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 5;
  transition: transform 0.4s;

  @media (min-width: 601px) {
    display: none;
  }
}

.disable {
  transform: translateX(150%);
}
