.root {
  width: 100%;
  margin-top: 10px;
  padding: 20px;
  background: linear-gradient(150.76deg, #6f99eb -8.39%, #000000 53.72%),
    #0a0d18;
  border-radius: 8px;
  box-shadow: 0px 24px 30px rgba(0, 0, 0, 0.19758) inset;

  transition: background 1s;
}
.new {
  background: linear-gradient(
      149.59deg,
      #cafca4 -31.63%,
      #000000 42.84%,
      #000000 42.84%
    ),
    #0a0d18;
}

.title {
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  font-size: 1rem;
  line-height: 19px;
  color: #cafca4;
  margin: 0;
  margin-bottom: 10px;
}
.text {
  font-family: "Gilroy", sans-serif;
  font-size: 0.875rem;
  line-height: 1rem;
  color: white;
  margin: 0;
}

.coinWrapper {
  position: relative;
  margin: 25px 0;

  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.coinImg {
  top: 0;
  left: 0;

  width: 26px;
  height: 26px;
  object-fit: contain;

  position: static;
}
.coinText {
  margin-left: 10px;
}

.button {
  width: 100%;
  height: 40px;
  border-radius: 8px;

  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.06) 0%,
    rgba(255, 255, 255, 0.07) 100%
  ) !important;
  border: 1px solid rgba(72, 53, 234, 0.6);

  color: white;
  letter-spacing: 1px;
  font-size: 0.9375rem;
  line-height: 18px;
  font-family: "Gilroy", sans-serif;
}
.disableButton {
  font-family: "Gilroy", sans-serif;
  font-size: 1.125rem;
  letter-spacing: 1px;
  line-height: 22px;
  font-weight: bold;
  color: #9f8dd0;
  opacity: 0.5;

  width: 100%;
  text-align: center;
  margin: 0;
}
