@import "sass/index";

.root {
  background: use(--onboarding-background);
  position: relative;
  overflow-x: hidden;
}
.wrapper {
  background-size: cover;
  background-position: center;
  background-image: var(--onboarding-gradient-backgroundImage);
}
.innerWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  position: relative;
  z-index: 1;
}
.header {
  display: flex;
  justify-content: center;
  padding: spacing(12) 0;
}
.logo {
  color: #00fffb;
}
.container {
  display: flex;
  justify-content: center;
  padding: 0;
}
.bannersWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: spacing(151);
}
.footer {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  padding: spacing(8) spacing(10.5);
  align-items: flex-end;
  flex: 1;

  color: use(--secondary-main, 0.5);
  &.Mui-focused {
    color: use(--secondary-main, 0.5);
  }
}
.breadcrumbs {
  & .MuiBreadcrumbs-li > a {
    text-decoration: none;
    color: use(--secondary-main, 0.5);
    &.Mui-.focused {
      color: use(--secondary-main, 0.5);
    }
  }
  & .MuiBreadcrumbs-.separator {
    margin: 0 spacing(2);
    color: use(--secondary-main, 0.5);
    &.Mui-.focused {
      color: use(--secondary-main, 0.5);
    }
  }
}

.closeBlock {
  position: absolute;
  top: spacing(4);
  right: spacing(4);

  @media (max-width: $mobileScreen) {
    position: static;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin-bottom: spacing(-8);
    margin-top: spacing(4);
    margin-right: spacing(4);
  }
}
.closeIcon {
  & path {
    fill: use(--template-modal-closeIcon-color);
    stroke: use(--template-modal-closeIcon-color);
  }
}

.christmasLogo {
  width: 256px;
  height: auto;
  margin: 0;
  object-fit: contain;
}

.scrollContainer {
  z-index: 1;
}
