@import "sass/index";

.walletContainer {
  min-width: 218px;
  height: 45px;
  border-radius: 100px;
  background: use(--info-dark-2);

  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin-left: 15px;

  @media (max-width: 500px) {
    margin: 0 0 0 7px;
    height: 35px;
    min-width: 163px;
  }
}

.walletButton {
  color: white;
  box-shadow: none;
  border-radius: 100px;
  width: auto;
  min-width: min-content;
  height: 100%;
  padding: 0;
  font-family: "Gilroy";
  font-weight: 700;
  font-size: 18px;
  text-transform: capitalize;
  background: transparent;
  z-index: 2;
  svg {
    height: 100%;
    width: auto;
  }

  @media (max-width: 500px) {
    font-size: 13px;
  }
}
.btnLabel {
  height: 100%;
}

.walletText {
  margin-left: 5px;
}

.walletIcon {
  overflow: visible;
  @media (max-width: 500px) {
    width: 19px;
  }
}
