@import "sass/index";

.root {
  width: 100%;
  min-height: 100%;
  display: flex;
  flex: 1;
  flex-direction: column;

  position: relative;
}
.content {
  flex: 1;
  position: relative;
}

.notification {
  position: fixed;
  top: 64px;
  right: 10px;
  z-index: 6;

  transition: transform 0.3s;
  transform-origin: 50% 0%;
  transform: scaleY(0);

  height: calc(100vh - #{$desktopHeaderSize});
}
.enable {
  transform: scaleY(1);
}
.notificationCloseOverlay {
  position: fixed;
  top: 0;
  right: 0;
  z-index: -1;

  background: transparent;
  width: 100vw;
  height: 100vh;
}
.mobileNotification {
  top: 56px;
  right: 0;
  height: calc(100% - 145px);
}
