.root {
  width: 320px;
  height: 100%;
  background: linear-gradient(131.49deg, #24375f 2.6%, #000000 60.63%), #0a0d18;
  border-radius: 10px;

  padding: 10px;
  padding-right: 5px;
  display: flex;
  flex-direction: column;
}
.mobileRoot {
  width: 100vw;
  height: 100%;
  max-width: 100vw;
  padding-right: 0;
  border-radius: 0;
  padding-bottom: 60px;
}
