@import "sass/index";
@import "../../variables.scss";

.titleWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1px;
}

.dailyWrapper {
  position: relative;
  width: 226.9px;
  height: 68.39px;
  left: 10px;
  animation: daily 4s 0s infinite alternate;

  @media (max-width: $mobileScreen) {
    width: 88.84px;
    height: 26.84px;
  }
}

.winWrapper {
  position: relative;
  width: 270.97px;
  height: 98.47px;
  animation: win 4s 0s infinite alternate;
  @media (max-width: $mobileScreen) {
    width: 106.05px;
    height: 35px;
    left: 6px;
  }
}

.title {
  position: absolute;
  font-family: "Gilroy";
  font-style: italic;
  font-weight: 1000;
  letter-spacing: 0.03em;
}

.dailyTitle {
  @extend .title;
  font-size: 75.7228px;
  line-height: 33px;
  @media (max-width: $mobileScreen) {
    font-size: 29.4597px;
    line-height: 13px;
  }
}

.winTitle {
  @extend .title;
  font-size: 126.174px;
  line-height: 55px;
  @media (max-width: $mobileScreen) {
    font-size: 49.0875px;
    line-height: 21px;
  }
}

.firsrLayer {
  color: rgba(52, 231, 255, 0.3);
}

.secondLayer {
  color: $primaryBlueColor;
}

.thirdLayer {
  color: transparent;
  -webkit-text-stroke: 0.5px white;
}

.dailyFirstLayer {
  @extend .dailyTitle;
  @extend .firsrLayer;
}

.dailySecondLayer {
  @extend .dailyTitle;
  @extend .secondLayer;
  top: -3px;
  left: 3px;
  @media (max-width: $mobileScreen) {
    top: -1px;
    left: 1px;
  }
}

.dailyThirdLayer {
  @extend .dailyTitle;
  @extend .thirdLayer;
  top: -5px;
  left: 6px;
  @media (max-width: $mobileScreen) {
    top: -1px;
    left: 2px;
  }
}

.winFirstLayer {
  @extend .winTitle;
  @extend .firsrLayer;
}

.winSecondLayer {
  @extend .winTitle;
  @extend .secondLayer;
  top: -4px;
  left: 5px;
  @media (max-width: $mobileScreen) {
    top: -2px;
    left: 2px;
  }
}

.winThirdLayer {
  @extend .winTitle;
  @extend .thirdLayer;
  top: -7px;
  left: 9px;
  @media (max-width: $mobileScreen) {
    top: -3px;
    left: 4px;
  }
}

.letterL {
  @media (min-width: $mobileScreen) {
    margin: 0 1px 0 3px;
  }
}

@keyframes daily {
  from {
    transform: scale(1.2);
  }
  to {
    transform: scale(1);
  }
}

@keyframes win {
  from {
    transform: scale(0.8);
  }
  to {
    transform: scale(1);
  }
}
