@import "sass/index";

.root {
}
.currencyBox {
  cursor: pointer;
  display: flex;
  background: #1f1f43;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 1.14286px;
  color: rgba(255, 255, 255, 0.6);
  padding: 7px;
  height: 40px;
  align-items: center;
  border-radius: 4px;

  & > svg {
    margin: 3px;
  }

  & > svg > g > g {
    stroke: rgba(255, 255, 255, 0.6);
  }
}
.rotateArrow {
  transform: rotate(180deg);
}
.icon {
  height: spacing(3);
  width: spacing(3);

  margin-right: spacing(1.5);
  object-fit: contain;

  @media (max-width: 360px) {
    margin-right: spacing(1);
  }
}
.close {
  width: 56px;
  height: 56px;
}
.popper {
  z-index: 5;
  background: linear-gradient(110.93deg, #24375f -15.02%, #000000 51.81%),
    #0a0d18;
  box-shadow: 0px 24px 30px rgba(0, 0, 0, 0.19758);
  border-radius: 10px;
  width: 100%;
  min-width: 320px;
  max-width: 400px;
  height: fit-content;
  margin-top: 10px;
  z-index: 1400;
}
.container {
  padding: 0px 5px 20px 20px;
}
.header {
  color: #ffffff;
  font-size: 22px;
  font-weight: bold;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.itemsContainer {
  $headerSize: 56;
  $footerSize: 56;
  $padding: 10;
  $siteFooterSize: 90;

  padding-right: 15px;
  overflow-y: scroll;
  max-height: 530px;
  height: calc(
    100vh - #{(($headerSize * 2) + $footerSize + $padding + $siteFooterSize)}px
  );
}
.fontFamily {
  font-weight: 500;
  font-family: "Gilroy", sans-serif;
}
.menuItem {
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;

  font-family: "Gilroy", sans-serif;
  font-weight: bold;

  color: #ffffff;
  padding: 6px 6px;
  font-size: 16px;
  border-radius: 8px;

  &:hover {
    border: 1px solid rgba(72, 53, 234, 0.572962);
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.0620629) 0%,
      rgba(255, 255, 255, 0.0755026) 100%
    ) !important;
  }

  @media (max-width: 400px) {
    font-size: 14px;
    height: 40px;
  }
}

.item {
}

.selectedItem {
  background: rgba(255, 255, 255, 0.1) !important;
}

.menuItemAnnex {
  font-family: "Gilroy", sans-serif;
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 1.07px;
  color: use(--primary-main, 0.5);
}

.coinName {
  white-space: nowrap;
}

.moneyBox {
  width: max-content;
  display: flex;
  flex-direction: column;

  & > span:last-child {
    font-size: 12px;
    color: #9f8dd0;
    letter-spacing: 1;
  }
}
.controlBox {
  padding: 10px 20px 0 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.divider {
  margin: 3px 5px;
  border-bottom: 1px solid #2e2b4a;
}
.button {
  cursor: pointer;
  background: transparent;
  border: 1px solid rgba(72, 53, 234, 0.6);
  border-radius: 8px;
  padding: 10px 60px;
  color: rgba(255, 255, 255, 0.8);
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  text-transform: uppercase;
  letter-spacing: 1px;

  &:hover {
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.0620629) 0%,
      rgba(255, 255, 255, 0.0755026) 100%
    );
  }
}
.switchBox {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.switchText {
  color: #ffffff;
  transition: opacity, 0.4s;
  letter-spacing: 1px;
  font-weight: bold;
  text-transform: uppercase;
}
.switchTextDisabled {
  opacity: 0.5;
}

.switchThumb {
  background-color: #2e2b4a;
  height: 14px;
  width: 28px;
  background-color: #161425;
  // margin-bottom: -4px;
}

.switchBoxRoot {
  margin-top: 5px;
}

.switchToggle {
  background: white;
  width: 20px;
  height: 20px;
}

.switchRoot {
  margin-right: 12px;
  margin-left: 12px;
}

.amountValue {
  color: white;
  letter-spacing: 1px;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 1rem;
  margin-top: 2px !important;
}
