@import "sass/index";

html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
strong,
b {
  font-weight: 700;
}
body {
  color: use(--template-footer-logos-pasino-background, 0.75);
  margin: 0;
  font-size: 0.875rem;
  font-family: "Gilroy";
  font-weight: 500;
  line-height: 1.43;
  background-color: use(--primary-main);
}
@media print {
  body {
    background-color: #fff;
  }
}
body::backdrop {
  background-color: use(--primary-main);
}
