@import "sass/index";

.catContainer {
  position: relative;
  width: 162.98px;
  height: 115%;
  margin-right: -10px;
  display: flex;
  z-index: 2;
  @media (max-width: $mobileScreen) {
    height: 120%;
    width: 64.22px;
  }
}

.cat {
  width: 162.98px;
  height: 203.04px;
  position: absolute;
  animation: cat 4s 0s infinite alternate;
  @media (max-width: $mobileScreen) {
    width: 63.41px;
    height: 78.99px;
  }
}

.paw {
  position: absolute;
  width: 80.05px;
  height: 62.35px;
  left: -22px;
  top: 65px;
  animation: paw 4s 0s infinite alternate;
  @media (max-width: $mobileScreen) {
    width: 31.14px;
    height: 24.26px;
    left: -10px;
    top: 25px;
  }
}

@keyframes cat {
  from {
    transform: translateY(-5%);
  }
  to {
    transform: translateY(3%);
  }
}

@keyframes paw {
  from {
    transform: rotate(-12deg);
  }
  to {
    transform: translateY(27%);
  }
}
