@import "sass/index";
@import "../../variables.scss";

.boardContainer {
  display: flex;
  align-items: center;
  justify-content: end;
  width: $boardContentWidth;
  height: $boardContentHeight;
  background-image: url("../../assets/board.webp");
  background-size: $boardContentWidth;
  background-repeat: no-repeat;
  @media (max-width: $mobileScreen) {
    width: $boardMobileContentWidth;
    height: $boardMobileContentHeight;
    background-size: 267.73px;
  }
}
