@import "sass/index";

.shadowTop {
  position: fixed;
  z-index: 50;
  top: 64px;
  left: 0;
  right: 0;
  pointer-events: none;
  height: spacing(2);
  opacity: 0;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.3) 0%,
    rgba(0, 0, 0, 0) 100%
  );

  @media (max-width: $mobileScreen) {
    top: 56px;
  }
}
.shadowBottom {
  z-index: 50;

  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  pointer-events: none;
  height: spacing(2);
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.3) 0%,
    rgba(0, 0, 0, 0) 100%
  );

  @media (max-width: $mobileScreen) {
    display: none;
  }
}
.root {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  min-height: 100vh;
}
.wrapper {
  margin-top: 64px;

  width: 100vw;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  justify-content: center;
  align-items: center;
}
.innerWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.block {
  background: use(--template-gameBlock-background);
  border-radius: 30px;
  display: flex;
  flex: 1;
  width: 100%;
  height: 3000px;
}
.chatBlock {
  background: use(--template-gameBlock-background);
  border-radius: 30px;
  display: flex;
  flex: 1;
  width: 280px;
  height: 3000px;
}

.gridWrapper {
  padding: 16px;

  @media (max-width: $mobileScreen) {
    padding: spacing(0);
  }

  width: 100%;
}
.gridInnerWrapper {
  display: flex;
  flex-direction: row;
  max-width: 100%;
}
.gridContentWrapper {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: spacing(2);
  width: calc(100% - #{$desktopChatWidth});

  @media (max-width: #{$swipedChatWidth}) {
    width: 100%;
    padding: spacing(2);
  }

  @media (max-width: 420px) {
    padding: 0;
  }
}

$padding: spacing(6); // chat padding 4; right content padding 2
$fullHD: 1920px;

.gridContentInnerWrapper {
  width: 100%;
  max-width: calc(#{$fullHD} - #{$desktopChatWidth} - #{$padding});
  position: relative;
}
.gridContentWrapperWithoutChat {
  width: 100%;
}
