$fontFamily: "Gilroy";
$primaryIndent: 10px;

$white: #ffffff;

$notificationWidth: 600px;
$notificationIndent: 10px;

@mixin notificationBackground($color) {
  background-color: $color;
}

.root {
  position: relative;
  display: flex;
  align-items: center;
  max-width: $notificationWidth;
  min-width: $notificationWidth / 2;
  width: 95%;
  margin-left: $primaryIndent;
  border-radius: 3px;
  user-select: none;
  padding-top: $notificationIndent;
  padding-bottom: $notificationIndent;
  animation-name: moveOut;
  animation-duration: 0.2s;
  margin-bottom: $notificationIndent;
  max-height: 200px;
  transition: max-height 0.3s;
  transform-origin: top;
}

.remove {
  animation-name: notifyBarLeaveAnimation;
  animation-duration: 0.5s;
  transform: translateX(-($notificationWidth + $primaryIndent));
  max-height: 0;
}

.info {
  @include notificationBackground(#43a0bb);
}

.success {
  @include notificationBackground(#43a047);
}

.warning {
  @include notificationBackground(#f99e1e);
}

.error {
  @include notificationBackground(#d32f2f);
}

@keyframes notifyBarLeaveAnimation {
  from {
    transform: translateX(0) scale(1);
    max-height: 200px;
    opacity: 1;
  }
  50% {
    max-height: 0;
  }
  to {
    transform: translateX(-($notificationWidth + $primaryIndent)) scale(0.1);
    max-height: 0;
    padding-top: 0;
    padding-bottom: 0;
    margin: 0;
    opacity: 0;
  }
}

@keyframes moveOut {
  from {
    transform: translateX(-($notificationWidth + $primaryIndent));
  }
  to {
    transform: translateX(0);
  }
}

.icon {
  margin-right: $primaryIndent * 2;
  margin-left: $primaryIndent * 1.5;
  width: 20px;
  height: 20px;
  fill: rgba($white, 0.9);
  color: rgba($white, 0.9);
}

.messageContainer {
  margin-right: 10px;
  min-width: min-content;
  font-weight: 500;

  & > h1 {
    margin: 0;
    font-family: $fontFamily;
    color: rgba($white, 0.8);
    font-size: 20px;
  }
  & > span {
    font-family: $fontFamily;
    color: rgba($white, 0.9);
    font-size: 14px;
  }
}

.close {
  margin-left: auto;
  margin-right: $primaryIndent;
  cursor: pointer;
  padding: 15px;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  transition: background-color 0.1s;
  &:hover {
    background-color: rgba(black, 0.1);
  }
  & > svg {
    fill: rgba($white, 0.9);
    width: 15px;
    height: 15px;
  }
}
