@import "sass/index";

$wheelSizeNormal: 310px;
$dailyWinWheelSize: 394px;
$dailyWinMobileWheelSize: 153.18px;
$coinSizeNormal: 26px;
$dailyWinCoinSize: 35px;
$dailyWinMobileCoinSize: 13.5px;
$dropShadowColor: rgba(103, 133, 199, 0.6);
$textStrokeColor: #4d4c4c;

.firstBorder {
  position: relative;
  background: linear-gradient(
    225deg,
    #fffb90 11.9%,
    #fbea78 22.33%,
    #f8dc65 29.53%,
    #e6c758 31.82%,
    #c5a041 36.72%,
    #ad8330 41.23%,
    #9e7226 45.14%,
    #996c22 48.03%,
    #9d7126 50.23%,
    #aa8131 52.99%,
    #be9b42 56.06%,
    #dabe5b 59.33%,
    #fbe878 62.5%,
    #ffffaa 68.84%,
    #fbe878 73.33%,
    #a4631b 85.62%
  );
  box-shadow: 0px 0px 80px 20px $dropShadowColor;
  padding: 10px;

  width: min-content;
  border-radius: 50%;
  user-select: none;
  &.dailyWin {
    padding: 16px;
    @media (max-width: $mobileScreen) {
      padding: 6.23px;
      box-shadow: 0px 0px 30px 10px $dropShadowColor;
    }
  }
}

.lightsContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2;

  &.spinning {
    transition: transform 5s;
    transform: rotate(
      calc(4 * 360deg + (-360deg * var(--selected-light) / var(--nb-light, 1)))
    );
  }
}

.secondBorder {
  background: linear-gradient(
      135deg,
      #fffb90 14.64%,
      #fbea78 24.69%,
      #f8dc65 31.63%,
      #e6c758 33.84%,
      #c5a041 38.56%,
      #ad8330 42.9%,
      #9e7226 46.67%,
      #996c22 49.46%,
      #9d7126 51.57%,
      #aa8131 54.24%,
      #be9b42 57.19%,
      #dabe5b 60.34%,
      #fbe878 63.4%,
      #ffffaa 69.5%,
      #fbe878 73.83%,
      #a4631b 85.67%
    ),
    #c4c4c4;
  padding: 5px;
  width: min-content;
  border-radius: 50%;
  &.dailyWin {
    padding: 8px;
    @media (max-width: $mobileScreen) {
      padding: 3.11px;
    }
  }
}

.thirdBorder {
  background: linear-gradient(
    45deg,
    #fffb90 10.61%,
    #fbea78 21.34%,
    #f8dc65 28.74%,
    #e6c758 31.09%,
    #c5a041 36.13%,
    #ad8330 40.76%,
    #9e7226 44.78%,
    #996c22 47.75%,
    #9d7126 50.01%,
    #aa8131 52.85%,
    #be9b42 56%,
    #dabe5b 59.36%,
    #fbe878 62.63%,
    #ffffaa 69.13%,
    #fbe878 73.75%,
    #a4631b 86.38%
  );

  padding: 2.5px;
  width: min-content;
  border-radius: 50%;
  &.dailyWin {
    padding: 4px;
    @media (max-width: $mobileScreen) {
      padding: 1.56px;
    }
  }
}

.wheelContainer {
  position: relative;
  width: $wheelSizeNormal;
  height: $wheelSizeNormal;
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 0.9) 25%,
    rgba(255, 255, 255, 0) 45%,
    rgba(255, 255, 255, 1) 85%,
    rgba(255, 255, 255, 0.5) 90%,
    rgba(255, 255, 255, 0) 93.15%,
    rgba(0, 0, 0, 0.8) 100%
  );
  margin: auto;
  border-radius: 50%;

  &::before {
    right: 0px;
  }

  &::after {
    right: -5px;
    border-right-color: transparent;
  }

  &.dailyWin {
    width: $dailyWinWheelSize;
    height: $dailyWinWheelSize;
    @media (max-width: $mobileScreen) {
      width: $dailyWinMobileWheelSize;
      height: $dailyWinMobileWheelSize;
    }
  }
}

.wheel {
  position: relative;
  width: $wheelSizeNormal;
  height: $wheelSizeNormal;

  overflow: hidden;
  border-radius: 50%;
  background-color: transparent;

  &.spining {
    transition: transform 5s;
    transform: rotate(
      calc(4 * 360deg + (-360deg * var(--selected-item) / var(--nb-item, 1)))
    );
  }

  &.dailyWin {
    width: $dailyWinWheelSize;
    height: $dailyWinWheelSize;
    @media (max-width: $mobileScreen) {
      width: $dailyWinMobileWheelSize;
      height: $dailyWinMobileWheelSize;
    }
  }
}

.wheelItemTemplate {
  position: absolute;
  box-sizing: border-box;
  top: 50%;
  left: 50%;
  width: 50%;
  transform-origin: center left;
  transform: translateY(-50%)
    rotate(calc(var(--item-nb) * (360deg / var(--nb-item, 1))));
  color: white;
  text-align: right;
}

.wheelItemBeforeTemplate {
  content: " ";
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding-left: 0px;
  opacity: 0.6;
  border: solid transparent calc(var(--slice-width) / 1.1);
  border-left: solid transparent 0;
  border-right: solid var(--item-back-col) calc(var(--slice-width) * 4.6);
}

.wheelItem {
  @extend .wheelItemTemplate;
  &::before {
    @extend .wheelItemBeforeTemplate;
    --slice-max-width: calc(
      3.14159 * #{$wheelSizeNormal} + #{$wheelSizeNormal / 2}
    );
    --slice-width: calc((var(--slice-max-width) / var(--nb-item)) - 36px);
  }
}

.wheelItemDailyWin {
  @extend .wheelItemTemplate;
  &::before {
    @extend .wheelItemBeforeTemplate;
    --slice-max-width: calc(
      3.14159 * #{$dailyWinWheelSize} + #{$dailyWinWheelSize / 2}
    );
    --slice-width: calc((var(--slice-max-width) / var(--nb-item)) - 46px);
    @media (max-width: $mobileScreen) {
      --slice-max-width: calc(
        3.14159 * #{$dailyWinMobileWheelSize} + #{$dailyWinMobileWheelSize / 2}
      );
      --slice-width: calc((var(--slice-max-width) / var(--nb-item)) - 18px);
    }
  }
}

.itemWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding-right: 5px;
  gap: 3px;
  font-size: 21px;
  font-weight: 700;
  -webkit-text-stroke: 1px $textStrokeColor;

  &.dailyWin {
    padding-right: 12px;
    gap: 4px;
    font-size: 24.0245px;
    font-weight: 1000;
    line-height: 21px;
    @media (max-width: $mobileScreen) {
      font-size: 9.34668px;
      line-height: 8px;
      padding-right: 5px;
      gap: 2px;
      -webkit-text-stroke: 0.5px $textStrokeColor;
    }
  }
}

.coin {
  width: $coinSizeNormal;
  height: $coinSizeNormal;

  &.dailyWin {
    width: $dailyWinCoinSize;
    height: $dailyWinCoinSize;
    @media (max-width: $mobileScreen) {
      width: $dailyWinMobileCoinSize;
      height: $dailyWinMobileCoinSize;
    }
  }
}

.lightWrapper {
  position: absolute;
  box-sizing: border-box;
  top: 50%;
  left: 50%;
  width: 50%;
  transform-origin: center left;
  transform: translateY(-50%)
    rotate(calc(var(--light-nb) * (360deg / var(--nb-light, 1))));
  color: white;
  text-align: right;
}

.light {
  position: absolute;
  top: -45px;
  right: 0;
  width: $coinSizeNormal;
  height: $coinSizeNormal;
  z-index: 100;
  animation: light 1s 0s infinite alternate;
}
.lightD {
  animation: light 1s 0s infinite alternate-reverse;
}
.arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-40%, -50%);

  &.dailyWin {
    width: 118px;
    @media (max-width: $mobileScreen) {
      width: 46px;
    }
  }
}

@keyframes light {
  from {
    transform: scale(0.1);
  }
  to {
    transform: scale(2);
  }
}

@media (max-width: $mobileScreen) {
  @keyframes light {
    from {
      transform: scale(0.1);
    }
    to {
      transform: scale(1);
    }
  }
}
