@import "sass/index";

.paper {
  background: linear-gradient(130.53deg, #24375f 2.16%, #000000 60.27%);
  border-radius: 30px;
  width: 440px;
  height: max-content;

  @media (max-width: $mobileScreen) {
    width: 100%;
    border-radius: 0;
    overflow: hidden;
    height: 100%;
  }
}
.root {
  height: max-content;
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
  overflow: hidden;
}
.closeIcon {
  align-self: flex-end;
}
.rewardContainer {
  cursor: pointer;
  position: absolute;
  background: rgba(0, 0, 0, 0);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 0;
  height: 0;
  overflow: hidden;
  transition: background 0.4s;
}
.rewardContainerVisible {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}
.rewardBorder {
  margin-top: 130px;
  border: none;
  background: linear-gradient(
    107deg,
    rgba(89, 63, 21, 1) 0%,
    rgba(213, 190, 113, 1) 78%,
    rgba(255, 201, 0, 1) 100%
  );
  border-radius: 25px;
  padding: 5px;
  transform: scale(0);
  transition: all 0.3s;
}
.reward {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-weight: bold;
  width: 280px;
  height: 160px;
  border-radius: 20px;
  color: #ffffff;

  background: linear-gradient(180deg, #292929 0%, #443d2b 51.04%, #292929 100%);
}
.rewardVisible {
  transform: scale(1);
}
.rewardHeader {
  font-size: 40px;
  -webkit-text-stroke: 1px #999999;
}
.rewardVal {
  display: flex;
  font-size: 20px;
  line-height: 24px;
  gap: 5px;
  align-items: center;

  & > img {
    width: 26px;
    height: 26px;
  }
}
.wheelsContainer {
  width: 360px;
  height: 380px;
  & > img {
    width: 26;
    height: 26;
  }
}
.wheelsContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-self: center;
  min-height: 395px;
}
.buttonContainer {
  position: relative;
  display: flex;
  justify-content: center;
}
.button {
  position: absolute;
  cursor: pointer;
  top: 30%;
  border-radius: 25px;

  border: none;
  padding: 2px;
  background: linear-gradient(
    90deg,
    rgba(39, 2, 64, 1) 0%,
    rgba(135, 4, 191, 1) 50%,
    rgba(39, 2, 64, 1) 100%
  );
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(1) translateY(0);
  transition: all 0.3s;
  & > div {
    border-radius: 25px;
    padding: 14px 72px;
    background: linear-gradient(276.53deg, #3d0463 -93.34%, #55038c 82.6%);
  }
  &:hover {
    & > div {
      background: linear-gradient(276.53deg, #3d0463 -93.34%, #430d67 82.6%);
    }
  }
  &:disabled {
    cursor: default;
    transform: scale(0.9) translateY(2px);
    background: linear-gradient(
      90deg,
      rgba(135, 4, 191, 1) 0%,
      rgba(39, 2, 64, 1) 50%,
      rgba(135, 4, 191, 1) 100%
    );
    & > div {
      background: linear-gradient(276.53deg, #3d0463 -93.34%, #430d67 82.6%);
    }
  }
}
.hideButton {
  overflow: hidden;
  transform: scale(0) translateY(2px) !important;
}
.img {
  position: absolute;
  width: 100%;
  top: 30px;
  left: 0;
}
.disable {
  display: none;
}
