@import "sass/index";

.menu {
  background: #cec9d6;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
}
.triangle {
  position: absolute;
  top: 6px;
  left: 42%;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;

  border-bottom: 4px solid #cec9d6;

  @media (max-width: $mobileScreen) {
    top: calc(100% - 10px);
    left: 72%;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-bottom: none;
    border-top: 4px solid #cec9d6;
  }
}

.menuItem {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 11px;
  line-height: 13px;
  font-weight: 500;
  color: #000000;
  padding: 7px 15px;
  transition: background 0.4s ease;

  &:hover {
    background: #b6b3bb !important;
  }

  @media (max-width: $mobileScreen) {
    font-size: 1rem;
    font-weight: bold;
    padding: 10px 16px;
  }
}
.icon {
  width: 20px;
  height: 20px;
  margin-right: 15px;
  img {
    width: 100%;
    height: 100%;
  }
}
