@import "sass/index";

@mixin mobileFontSize {
  font-weight: 700;
  font-size: 13.4236px;
}
.root {
}
.popper {
  z-index: 5;
  border-radius: 30px;
  background: linear-gradient(
      135deg,
      #3b1871 0%,
      rgba(11, 4, 21, 0.181709) 48.48%,
      rgba(3, 1, 4, 0.0001) 80.79%,
      #000000 100%
    ),
    #0a0d18;
  box-shadow: 0px 24px 30px rgba(0, 0, 0, 0.19758);
  width: 100%;
  min-width: 320px;
  max-width: 413px;
  height: fit-content;
  margin-top: 10px;
  z-index: 1400;
}
.container {
  padding: 0px 5px 14px 17px;
}

.header {
  color: #ffffff;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: space-between;

  margin-bottom: 15px;
}
.close {
  width: 56px;
  height: 56px;
}
.itemsContainer {
  $headerSize: 56;
  $footerSize: 56;
  $padding: 10;
  $siteFooterSize: 90;

  padding-right: 10px;
  overflow-y: scroll;
  max-height: 520px;
  height: calc(
    100vh - #{(($headerSize * 2) + $footerSize + $padding + $siteFooterSize)}px
  );
  &::-webkit-scrollbar {
    width: 4px;
    background: rgba(0, 0, 0, 0.1);
  }

  &::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.5);
    border-radius: 9.08864px;
  }
  &::-webkit-scrollbar-thumb {
    background: rgba(159, 141, 208, 0.5);
    border-radius: 10px;
  }
  margin-right: 5px;
}

.selectedItem {
  background: rgba(255, 255, 255, 0.1) !important;
}
.fontFamily {
  font-weight: 500;
  font-family: "Gilroy", sans-serif;
}

.menuItem {
  height: 51px;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px;

  background: rgba(255, 255, 255, 0.1) !important;
  mix-blend-mode: normal;
  border-radius: 10px;

  font-family: "Gilroy", sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;

  color: #ffffff;
  padding: 16px 15px;

  &:hover {
    border: 1px solid rgba(72, 53, 234, 0.572962);
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.0620629) 0%,
      rgba(255, 255, 255, 0.0755026) 100%
    ) !important;
  }

  @media (max-width: 600px) {
    font-size: 14px;
    height: 44px;
  }
}

.icon {
  height: 28.5px;
  width: 28.5px;

  margin-right: spacing(1.5);
  object-fit: contain;

  @media (max-width: 360px) {
    margin-right: spacing(1);
    height: 23px;
    width: 23px;
  }
}

.coinName {
  white-space: nowrap;
  text-transform: capitalize;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;

  @media (max-width: 600px) {
    font-size: 14px;
  }
}

.grow {
  flex: 1 1 auto;
}

.moneyBox {
  width: min-content;
  display: flex;
  flex-direction: column;

  & > span:last-child {
    font-size: 12px;
    color: #9f8dd0;
    letter-spacing: 1;
  }
}

.coinValue {
  overflow: hidden;
  text-overflow: ellipsis;
  margin-left: 10px;
  color: #ffffff;

  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.856209px;
}

.menuItemAnnex {
  font-family: "Gilroy", sans-serif;
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 1.07px;
  color: use(--primary-main, 0.5);
}

.divider {
  margin: 3px 5px;
  border-bottom: 1px solid #2e2b4a;
}

.controlBox {
  padding: 12px 20px 0 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.switchBox {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.switchBoxRoot {
  font-weight: 700;
  font-size: 12px;
  line-height: 24px;
  letter-spacing: 0.932272px;

  color: #ffffff;

  display: flex;
  height: 24px;
  align-items: center;
}
.switchTitle {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  margin-bottom: 5px;
}
.switchRoot {
  margin: 0 8px 0 10px;
}
.switchThumb {
  background: rgba(255, 255, 255, 0.15);
  mix-blend-mode: normal;
  border-radius: 41.8746px;
  width: 41px;
  height: 14px;
}
.switchToggle {
  width: 23px;
  height: 23px;
  background: #ffffff;
  border-radius: 58.6883px;
}

.amountBox {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-self: baseline;
  & span {
    display: block;
    min-width: fit-content;
  }
}
.amountBoxTitle {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  margin-bottom: 5px;
  text-align: center;
}
.amountValue {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  text-align: right;
  letter-spacing: 0.611578px;
  color: #ffffff;
  text-transform: uppercase;

  display: flex;
  height: 24px;
  align-items: center;
}
.triangle {
  top: -9px;
  color: #1b1037 !important;
}

@media (max-width: 600px) {
  .switchTitle,
  .amountBoxTitle {
    @include mobileFontSize;
  }
  .switchBoxRoot {
    font-weight: 700;
    font-size: 10.0677px;
    line-height: 12px;
    letter-spacing: 0.782154px;
  }
  .amountValue {
    @include mobileFontSize;
    letter-spacing: 0.513099px;
  }
  .coinName {
    @include mobileFontSize;
  }
  .coinValue {
    font-weight: 600;
    font-size: 15.1016px;
    line-height: 18px;
    letter-spacing: 0.718339px;
  }
}
