@import "sass/index";
@import "../../variables.scss";

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 2;
  margin-right: 84px;
  margin-bottom: 35px;
  @media (max-width: $mobileScreen) {
    margin-right: 28px;
    margin-bottom: -11px;
  }
}

.title {
  position: relative;
  right: 9px;
  transform: rotate(-8.23deg);
  margin-bottom: 8px;
  @media (max-width: $mobileScreen) {
    margin-bottom: 4px;
    right: 5px;
  }
}

.button {
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 210.75px;
  height: 52.06px;
  background: linear-gradient(
    180deg,
    $primaryBlueColor 0%,
    $primaryBlueColor 100%
  );
  border: none;
  border-radius: 42px;
  margin-top: 40px;
  animation: button 2.5s 0s infinite alternate;
  &:after {
    box-shadow: 0px 0px 5px 5px white;
    content: "";
    height: 100%;
    opacity: 0.4;
    position: absolute;
    left: -5%;
    transform: rotate(15deg);
    animation: buttonAfter 5s infinite;
    animation-timing-function: linear;
    @media (max-width: $mobileScreen) {
      box-shadow: 0px 0px 2px 2px white;
    }
  }

  @media (max-width: $mobileScreen) {
    width: 90px;
    height: 21px;
    margin-top: 15px;
  }
}

.buttonText {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  font-size: 18.5px;
  line-height: 27px;
  letter-spacing: 0.02em;
  color: #000000;
  @media (max-width: $mobileScreen) {
    font-size: 10px;
    line-height: 10px;
  }
}

@keyframes button {
  from {
    transform: scale(1.2);
    box-shadow: 0px 0px 10px 10px $dropShadowColor;
  }
  to {
    transform: scale(0.9);
  }
}

@media (max-width: $mobileScreen) {
  @keyframes button {
    from {
      transform: scale(1.2);
      box-shadow: 0px 0px 3px 3px $dropShadowColor;
    }
    to {
      transform: scale(0.9);
    }
  }
}

@keyframes buttonAfter {
  from {
    transform: translateX(0) rotate(15deg);
  }

  to {
    transform: translateX(calc(#{$buttonWidth} * 2)) rotate(15deg);
  }
}

@media (max-width: $mobileScreen) {
  @keyframes buttonAfter {
    from {
      transform: translateX(0) rotate(15deg);
    }

    to {
      transform: translateX(calc(#{$mobileButtonWidth} * 2)) rotate(15deg);
    }
  }
}
