@import "sass/index";

$maxWidth: 350px;

.root {
  position: fixed;
  width: 100%;
  bottom: 10px;
  left: 0;
  z-index: 10;

  display: flex;
  justify-content: center;

  padding: 0 10px;
}

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  max-width: $maxWidth;
  width: 100%;

  & > * {
    border-radius: 0;

    &:first-child {
      border-radius: 100px 0 0 100px;
    }

    &:last-child {
      border-radius: 0 100px 100px 0;
    }
  }
}

.background {
  position: absolute;
  bottom: 0;
  width: calc(100% - 20px);
  height: 52px;

  background-color: use(--template-bottomNavigation-background);
  border-radius: 100px;

  max-width: $maxWidth;
  box-shadow: 0px 0px 5px 1.5px rgba(0, 0, 0, 0.5);
}
