.text {
  margin: 0;
  font-size: 0.65rem;
  color: white;

  overflow-wrap: anywhere;
  font-weight: bold;

  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.172339);
  font-family: "Gilroy";
}

.textMin {
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.88px;
}

.textBig {
  font-size: 14.7px;
  line-height: 18px;
  letter-spacing: 1.176px;
}
