@import "sass/index";

.balanceElement {
  position: absolute;
  left: 18px;
  top: 25px;
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  font-size: 16px;
  color: use(--subsidiary-green);

  animation: effectIn 3000ms cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0;
  transform: translateY(100%) scale(0.6);

  @media (max-width: 500px) {
    font-size: 11px;
    left: 10px;
    top: 20px;
  }
}

@keyframes effectIn {
  0% {
    opacity: 0;
    transform: translateY(100%) scale(0.6);
  }
  50% {
    opacity: 1;
    transform: translateY(25%) scale(1);
  }
  100% {
    opacity: 0;
    transform: translateY(0%) scale(0.8);
  }
}
