@import "sass/index";

.root {
  background: transparent !important;
  box-shadow: none;

  padding: 0;
  height: 52px;
  width: 20%;
  color: use(--template-footer-logos-bottomNavigation-background);
}

.logo {
  height: fit-content;

  color: use(--template-bottomNavigation-background);

  & :global .baseButton__waveAnimation {
    display: none;
  }
}

.valueItem {
  background: #de3939;
  color: white;

  position: absolute;
  top: 10px;
  right: 35px;
  width: 15px;
  height: 15px;
  border-radius: 50%;

  font-family: "Varela Round", sans-serif;
  font-size: 10px;
}
